import React from 'react';
import {Alert} from "@vkontakte/vkui";

export default class Pop extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        };
    }
    openAlert = () => {
        this.setState({ popout:
                <Alert
                    actionsLayout="vertical"
                    actions={[{
                        title: '',
                        autoclose: true,
                        mode: 'cancel'
                    }]}
                    onClose={this.closeAlert}
                >
                    <h2> </h2>
                    <p>       2- .</p>
                </Alert>
        });
    };
    closeAlert = () => {
        this.setState({ popout: null });
    };
    render() {
       return (
           this.openAlert()
       )
    }
}